

































var userLoginInfo;
export default {
	data() {
		return {
			cardDetailList: [],
			cardId: 0
		}
	},
	onLoad(params) {
		this.cardId = params.id;
	},
	onShow() {
		userLoginInfo = this.userLoginInfo('../mine/mine', 2);
		// console.log(userLoginInfo.id)
		this.getDetailList();
	},
	methods: {
		// 获取银行卡详细信息
		getDetailList: function() {
			uni.request({
				url: this.apiUrl + 'user/getBank',
				method: 'GET',
				data: {
					uid: userLoginInfo.id,
					id: this.cardId
				},
				success: res => {
					// console.log(res.data.data);
					if (res.data.code === 0) {
						this.cardDetailList = res.data.data;
						// console.log(this.cardDetailList);
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 删除银行卡
		delectCard() {
			uni.request({
				url: this.apiUrl + 'user/delBank',
				method: 'GET',
				data: {
					uid: userLoginInfo.id,
					id: this.cardId
				},
				success: res => {
					// console.log(res);
					if (res.data.code === 0) {
						uni.showToast({
							title: "删除银行卡成功",
							icon: 'none', //图标，支持"success"、"loading" 
							mask: true,
							success: (res) => {
								setTimeout(() => {
									uni.redirectTo({
										url: './addCard'
									});
								}, 500);
							},
						})
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 对银行卡进行修改
		revise(cardDetailList) {
			// console.log(cardDetailList);
			var cartList = JSON.stringify(cardDetailList);
			// console.log(cartList);
			uni.navigateTo({
				url: './card_holding?cardInfo='+cartList
			})
		}

	},
}
